import { Component, OnInit, Input } from '@angular/core';
import { ApiService } from '../api.service';
import { NzMessageService } from 'ng-zorro-antd/message';
import { deserialize, ObjectID } from 'bson';

@Component({
  selector: 'app-collection-renderer',
  templateUrl: './collection-renderer.component.html',
  styleUrls: ['./collection-renderer.component.css']
})
export class CollectionRendererComponent implements OnInit {
  @Input() database: any;
  @Input() collection: any;
  documents: any;
  filter: any;
  loading: boolean = false;
  constructor(private API: ApiService, private message: NzMessageService) {}

  ngOnInit() {
    this.getDocuments();
  }

  getDocuments() {
    this.loading = true;
    this.API.getDocumentsByCollection(this.database, this.collection).subscribe(
      (documents: any) => {
        this.loading = false;
        this.documents = Object.values(
          deserialize(Buffer.from(documents.data))
        );
      }
    );
  }

  query() {
    try {
      this.loading = true;
      const filter = this.filter ? JSON.parse(this.filter) : {};
      this.API.filterDocumentsByQuery(
        this.database,
        this.collection,
        filter
      ).subscribe((documents: any) => {
        this.loading = false;
        this.documents = Object.values(
          deserialize(Buffer.from(documents.data))
        );
      });
    } catch (err) {
      console.log(err);
      this.loading = false;
    }
  }

  deleteDocument(id) {
    this.API.deleteDocumentById(this.database, this.collection, id).subscribe(
      () => {
        this.query();
      }
    );
  }

  copyToClipboard(text: string, msg: string) {
    text = JSON.stringify(text);
    let txtArea = document.createElement('textarea');
    txtArea.style.position = 'fixed';
    txtArea.style.top = '0';
    txtArea.style.left = '0';
    txtArea.style.opacity = '0';
    txtArea.value = text;
    document.body.appendChild(txtArea);
    txtArea.select();
    try {
      let result = document.execCommand('copy');
      if (result) this.message.success('Copied!');
    } catch (err) {}
    document.body.removeChild(txtArea);
  }
}
